*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0 auto;
  font-family: "Suisse Intl", "Arial", "Helvetica", "Helvetica CY", "Nimbus Sans";
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input::-ms-clear {
  display: none;
}

input:-moz-focusring {
  outline: none;
  border-color: #00adef;
  box-shadow: none;
}

input:invalid {
  outline: none;
  box-shadow: none;
}

.page-body {
  padding: 50px 0 50px;
}

.page-title {
  padding: 0 50px;
  margin-top: 0;
  margin-bottom: 20px;
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
  text-align: center;
  color: #888;
}

@keyframes scale-bounce {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

.card-block {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.card-block.size-4 {
  max-width: calc(70px * (4 + 3/4));
}

.card-block.size-4 .card:not(:nth-child(4n)) {
  margin-right: calc(70px/4);
}

.card-block.size-4 .card {
  margin-bottom: calc(70px/4);
}

.card-block.size-6, .card-block.size-8 {
  max-width: calc(70px * (6 + 5/4));
}

.card-block.size-6 .card:not(:nth-child(6n)),
.card-block.size-8 .card:not(:nth-child(6n)) {
  margin-right: calc(70px/4);
}

.card-block.size-6 .card,
.card-block.size-8 .card {
  margin-bottom: calc(70px/4);
}

.card {
  position: relative;
  width: 70px;
}

.card.done .card__content {
  transform: rotateY(180deg);
}

.card.opened {
  animation: scale-bounce 0.2s;
  animation-delay: 0.6s;
}

.card__content {
  position: relative;
  width: 100%;
  padding-top: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  padding: 5px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.card__front {
  background-color: lightblue;
}

.card__back {
  background-color: #eee;
  transform: rotateY(180deg);
  border: 2px solid #888;
}

@media (max-width: 349px) {
  .card-block {
    justify-content: space-around;
  }
  .card-block.size-4 .card:not(:nth-child(4n)) {
    margin-right: 0;
  }
  .card-block.size-4 .card {
    margin-bottom: 3%;
    width: 22%;
  }
}

@media (max-width: 559px) {
  .card-block {
    justify-content: space-around;
  }
  .card-block.size-6, .card-block.size-8 {
    max-width: 100%;
  }
  .card-block.size-6 .card:not(:nth-child(6n)),
  .card-block.size-8 .card:not(:nth-child(6n)) {
    margin-right: 0;
  }
  .card-block.size-6 .card,
  .card-block.size-8 .card {
    margin-bottom: 3%;
    width: 15%;
  }
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  visibility: visible;
  overflow: hidden;
  z-index: 100;
}

.menu__container {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 1;
  transition: top 1s, opacity 0.2s linear 0s;
  box-shadow: 0 3.8px 5.6px rgba(0, 0, 0, 0.038), 0 8.7px 12.7px rgba(0, 0, 0, 0.056), 0 15.1px 22.1px rgba(0, 0, 0, 0.069), 0 23.9px 35.1px rgba(0, 0, 0, 0.08), 0 36.9px 54.1px rgba(0, 0, 0, 0.091), 0 57.6px 84.4px rgba(0, 0, 0, 0.104), 0 95.6px 140.1px rgba(0, 0, 0, 0.122), 0 191px 280px rgba(0, 0, 0, 0.16);
  z-index: 101;
}

.menu__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu__title {
  margin: 0;
  margin-bottom: 10px;
  font-size: 24px;
  line-height: 30px;
}

.menu__subtitle {
  margin: 0;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 22px;
}

.menu__radio:checked + label {
  border-color: #555;
}

.menu__label {
  padding: 20px;
  width: 150px;
  margin-bottom: 15px;
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 10px;
  transition: all 0.3s;
}

.menu__submit {
  padding: 20px;
  width: 150px;
  color: #fff;
  text-align: center;
  border: none;
  border-radius: 10px;
  background-color: darkblue;
}

.menu.js-hidden {
  visibility: hidden;
  transition: visibility 0s linear 1s;
}

.menu.js-hidden .menu__container {
  opacity: 0;
  top: -100%;
  transition: top 1s, opacity 0.3s linear 0.7s;
}

.player-wrap {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
  overflow-y: auto;
}

.player-wrap::after {
  content: "";
  width: 0;
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.player-wrap.js-hidden {
  display: none;
}

.player-content {
  display: inline-block;
  width: 90%;
  margin-top: 50px;
  margin-bottom: 50px;
  vertical-align: middle;
}

.player-new-game {
  padding: 20px;
  margin-top: 20px;
  color: #fff;
  text-transform: uppercase;
  background-color: rgba(255, 255, 255, 0.1);
  border: 2px solid #fff;
  border-radius: 10px;
  transition: all 0.3s;
}

.player-new-game:hover, .player-new-game:active {
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.2);
}
